import { ShoppingCart } from "lucide-react";

interface HeaderProps {
  cartItemCount: number;
  onCartClick: () => void;
}

const Header: React.FC<HeaderProps> = ({ cartItemCount, onCartClick }) => {
  return (
    <header className="bg-slate-900 text-white py-5 shadow-md sticky top-0 z-50">
      <div className="container mx-auto flex justify-between items-center px-4">
        <a href="#" className="text-3xl font-bold tracking-tight">
          <span className="text-sky-400">Pro</span>Health
        </a>
        <button onClick={onCartClick} className="relative flex items-center space-x-2 p-2 rounded-lg hover:bg-slate-700 transition-colors">
          <ShoppingCart size={28} />
          <span className="hidden sm:inline">Cart</span>
          {cartItemCount > 0 && (
            <span className="absolute -top-2 -right-2 bg-red-500 text-white text-xs font-semibold rounded-full h-6 w-6 flex items-center justify-center border-2 border-slate-900">
              {cartItemCount}
            </span>
          )}
        </button>
      </div>
    </header>
  );
};
export default Header;