// components/doctors/ContactDoctorModal.tsx
"use client";
import React, { useState, useEffect } from "react";
import Modal from './Modal';
import { Doctor } from './types';

interface ContactDoctorModalProps {
  isOpen: boolean;
  onClose: () => void;
  doctor: Doctor | null;
  // Modified onSubmit to return more data for tracking
  onSubmit: (formData: { name: string; email: string; message: string; doctorName: string; doctorId: string; }) => void;
}

const ContactDoctorModal: React.FC<ContactDoctorModalProps> = ({ isOpen, onClose, doctor, onSubmit }) => {
  const [formData, setFormData] = useState({ name: "", email: "", message: "" });

  useEffect(() => {
    if (isOpen) {
      setFormData({ name: "", email: "", message: "" });
    }
  }, [isOpen, doctor]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!doctor) return;
    
    if (!formData.name.trim() || !formData.email.trim() || !formData.message.trim()) {
        alert("Please fill in all fields.");
        return;
    }
    if (!/\S+@\S+\.\S+/.test(formData.email)) {
        alert("Please enter a valid email address.");
        return;
    }
    
    // Pass doctorId as well for tracking
    onSubmit({ ...formData, doctorName: doctor.name, doctorId: doctor.id });
    onClose();
  };

  if (!doctor) return null;

  return (
    <Modal isOpen={isOpen} onClose={onClose} title={`Contact Dr. ${doctor.name}`}>
      <form onSubmit={handleSubmit} className="space-y-5">
        <p className="text-sm text-slate-600">
          Your message will be sent to Dr. {doctor.name} through our secure platform.
          They will respond to your provided email address.
        </p>
        <div>
          <label htmlFor="contactName" className="block text-sm font-medium text-slate-700 mb-1">Your Name</label>
          <input
            type="text"
            name="name"
            id="contactName"
            value={formData.name}
            onChange={handleChange}
            className="w-full p-3 border border-slate-300 rounded-md focus:ring-2 focus:ring-sky-500 focus:border-sky-500 outline-none text-slate-700"
            required
          />
        </div>
        <div>
          <label htmlFor="contactEmail" className="block text-sm font-medium text-slate-700 mb-1">Your Email</label>
          <input
            type="email"
            name="email"
            id="contactEmail"
            value={formData.email}
            onChange={handleChange}
            className="w-full p-3 border border-slate-300 rounded-md focus:ring-2 focus:ring-sky-500 focus:border-sky-500 outline-none text-slate-700"
            required
          />
        </div>
        <div>
          <label htmlFor="contactMessage" className="block text-sm font-medium text-slate-700 mb-1">Message</label>
          <textarea
            name="message"
            id="contactMessage"
            rows={4}
            value={formData.message}
            onChange={handleChange}
            className="w-full p-3 border border-slate-300 rounded-md focus:ring-2 focus:ring-sky-500 focus:border-sky-500 outline-none text-slate-700"
            required
          />
        </div>
        <button
          type="submit"
          className="w-full bg-sky-500 text-white py-3 px-6 rounded-lg hover:bg-sky-600 transition-colors font-semibold text-base"
        >
          Send Message
        </button>
      </form>
    </Modal>
  );
};

export default ContactDoctorModal;